import React from 'react'
import s from './withTwo.module.css'
import { useRef } from 'react'
import Modal from '../../../Modal/Modal'
import TwoImg from '../../../images/WithTwo.png'
import { Tooltip } from '@mui/material'
import { calculate_d, calculate_D1 } from '../../../store/electricWithTwo'
import { useDispatch, useSelector } from 'react-redux'
import { setParametr } from '../../../store/electricWithTwo'

function WithTwo() {
    let modalText =
        '      -             .    :  ,  ,     ,      ,    -,    ,   .       :  ,     .'
    const dispatch = useDispatch()

    const { d, D2, D1, U, C, K } = useSelector(
        (state) => state.electric_withTwo
    )
    // let { U } = useSelector((state) => state.electricWithCase)

    return (
        <div className={s.wrapper}>
            <div className={s.content}>
                <div className={s.OneColomn}>
                    <Modal text={modalText} />
                    <b>      </b>
                    <img className={s.twoImg} src={TwoImg} />
                    <b>  :</b>
                    <div className={s.item}>
                        <span>U, </span>
                        <input
                            type="number"
                            value={U}
                            onChange={(event) =>
                                dispatch(
                                    setParametr({
                                        parametr: 'U',
                                        ref: event.target.value,
                                    })
                                )
                            }
                        />
                    </div>
                    <div className={s.item}>
                        <span>D2, </span>
                        <input
                            type="number"
                            value={D2}
                            onChange={(event) =>
                                dispatch(
                                    setParametr({
                                        parametr: 'D2',
                                        ref: event.target.value,
                                    })
                                )
                            }
                        />
                    </div>
                    <div className={s.item}>
                        ,      {' '}
                        <br />
                            , 
                          :
                    </div>
                    <div className={s.item}>
                        <Tooltip
                            describeChild
                            title="C - ,    ,       "
                        >
                            <span>C, /^2 </span>
                        </Tooltip>
                        <input
                            type="number"
                            value={C}
                            step="0.1"
                            onChange={(event) =>
                                dispatch(
                                    setParametr({
                                        parametr: 'C',
                                        ref: event.target.value,
                                    })
                                )
                            }
                        />
                    </div>
                    <div className={s.item}>
                        <Tooltip
                            describeChild
                            title=" - ,    ,       "
                        >
                            <span>K</span>
                        </Tooltip>
                        <input
                            type="number"
                            value={K}
                            step="0.1"
                            onChange={(event) =>
                                dispatch(
                                    setParametr({
                                        parametr: 'K',
                                        ref: event.target.value,
                                    })
                                )
                            }
                        />
                    </div>
                    <button
                        className={s.button1}
                        onClick={() => {
                            let d1 = U / C
                            let d2 = 1 / K
                            dispatch(calculate_d({ d1, d2 }))
                            dispatch(calculate_D1({ D2, U }))
                        }}
                    >
                        {' '}
                        
                    </button>
                </div>
                <div className={s.TwoColomn}>
                    <div className={s.resultsItem}>
                        <b> :</b>
                    </div>
                    <div className={s.item}>
                          
                    </div>
                    <div className={s.item}>
                        <span> d,</span>
                        <input type="number" value={d} disabled />
                    </div>
                    <div className={s.item}>
                        <span>D1,</span>
                        <input type="number" value={D1} disabled />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default WithTwo
